@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: url(/rickmorty-web/static/media/home-bg.d6eeff95.jpg) no-repeat center fixed;
  background-size: cover;
}

@media all and (max-width: 525px) {
  .App {
    background: url(/rickmorty-web/static/media/phone-bg.ab8ed4e6.jpg) no-repeat center fixed;
    background-size: cover;
  }
}

.home {
  display: flex;
  width: 100%;
  height: 100%;
}

.home-content {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
}

.home-body {
  display: grid;
  grid-template-columns: repeat(5, 16%);
  grid-template-rows: repeat(5, 42%);
  grid-row-gap: 10px;
  grid-column-gap: 20px;
  width: 100%;
  overflow-y: hidden;
  justify-content: center;
  margin-top: -140px;
  padding-bottom: 140px;
}

.searcher-grid {
  display: grid;
  grid-column-start: 1;
  grid-column-end: 6;
  align-self: center;
  justify-self: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
  padding-top: 160px;
}

@media all and (max-width: 1200px) {
  .home-body {
    grid-template-columns: repeat(4, 19%);
    grid-template-rows: repeat(6, 40%);
  }

  .searcher-grid {
    grid-column-start: 1;
    grid-column-end: 5;
  }
}

@media all and (max-width: 950px) {
  .home-body {
    grid-template-columns: repeat(3, 25%);
    grid-template-rows: repeat(8, 40%);
  }

  .searcher-grid {
    grid-column-start: 1;
    grid-column-end: 4;
  }
}

@media all and (max-width: 650px) {
  .home-body {
    grid-template-columns: repeat(2, 38%);
    grid-template-rows: repeat(11, 42%);
  }

  .searcher-grid {
    grid-column-start: 1;
    grid-column-end: 3;
    padding-top: 220px;
  }
}

.modal-content {
  position: absolute;
  height: 80%;
  width: 50%;
  border: 1px solid rgba(0, 0, 0, 0.95);
  background: rgba(0, 0, 0, 0.95);
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  border-radius: 4px;
  outline: none;
  padding: 20px;
  box-shadow: 0px 10px 100px 10px rgba(162, 0, 255, 0.459);
  font-family: monospace;
}

.modal-container {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  flex-direction: column;
}

.close-modal-button {
  display: flex;
  justify-content: center;
  width: 100%;
}

.close-button {
  font-size: 30px;
  color: rgb(214, 214, 214);
}

.modal-image-container {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 50%;
  object-fit: contain;
  margin-top: 10px;
}

.modal-image {
  height: 100%;
  object-fit: contain;
}

.modal-text {
  display: flex;
  flex-direction: row;
  height: 40px;
  color: white;
}

.modal-title {
  color: rgb(119, 94, 126);
}

.modal-name-text {
  color: rgb(123, 255, 0);
  text-align: center;
}

.pad-left {
  padding-left: 30px;
}

.data-cont {
  width: 100%;
  height: 100%;
  font-size: 13px;
}

.center-text {
  display: flex;
  width: 100%;
  text-align: center;
  justify-content: center;
  font-weight: lighter;
}

.type {
  margin-left: -60px;
}

.genre {
  margin-left: -80px;
}

.species {
  margin-left: -90px;
}

@media all and (max-width: 950px) {
  .modal-content {
    width: 60%;
  }

  .modal-container {
    height: 95%;
  }

  .data-cont {
    height: 30%;
  }

  .modal-name-text {
    margin: 0;
  }

  .modal-text {
