.switch {
  position: absolute;
  display: inline-block;
  width: 30%;
  height: 78%;
  top: 4px;
  right: -5%;
}

.switch input {
  display: none;
}

.slider {
  position: absolute;
  z-index: 999;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(102, 255, 0, 0.753);
  box-shadow: 0px 8px 30px 0px rgba(94, 255, 0, 0.205);
  transition: 1s;
  border-radius: 34px;
}

.slider:active {
  box-shadow: 0px 10px 100px 0px rgb(98, 0, 255);
  transition: 0ms;
}

.slider:before {
  position: absolute;
  content: '';
  height: 23px;
  width: 23px;
  left: 6px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: rgba(98, 0, 255, 0.76);
  box-shadow: 0px 10px 50px 0px rgba(98, 0, 255, 0.247);
  transition: 1s;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(55px);
}

input:checked + .slider:active {
  box-shadow: 0px 10px 100px 0px rgba(94, 255, 0, 0.425);
  transition: 0ms;
}

/*------ ADDED CSS ---------*/
.slider:after {
  z-index: -1;
  position: relative;
  content: 'NAME';
  color: rgb(255, 255, 255);
  display: block;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  font-size: 10px;
  font-family: Verdana, sans-serif;
}

input:checked + .slider:after {
  content: 'TYPE';
}

